/*colors*/

$backgroundApp: #1d4b70;
$backgroundModule: #118eed;
$backgroundWhite: #fff;

$backgroundNone: none;

$red: red;
$blue: blue;
$black: #000;
$white: #fff;
$gray: #ededed;

/*fonts families*/
$arial: Arial, Helvetica, sans-serif;

/*paddings, margins and borders*/

$TwentyEven: 20px;
$TenEven: 10px;
$FiveEven: 5px;
$TenHorizontal: 10px 0;
$TenVertical: 0 10px;
$borderNone: none;

/*Styles*/
body {
  background-color: $backgroundApp;
  font-family: $arial;
}

.entryForm,
.resultForms {
  background-color: $backgroundModule;
  padding: $TwentyEven;
  margin: $TenHorizontal;
}

h1 {
  color: $white;
}

.entryForm input,
.editForm input {
  background: $backgroundNone;
  border: $backgroundNone;
  color: $white;
  border-bottom: $backgroundApp 1px solid;
  height: 50px;
  margin: $TenVertical;
  padding: $TenVertical;
}

.entryForm input::placeholder,
.editForm input::placeholder {
  color: $gray;
}
.entryForm input:focus,
.editForm input:focus {
  outline: none;
  background: $backgroundWhite;
  color: $black;
}

.entryForm button,
.Result button,
.editForm button {
  height: 50px;
  width: 80px;
  margin: $TenEven;
  border: 0;
  color: $gray;
  border-radius: 5px;
  cursor: pointer;
}
.Result {
  background-color: $white;
  display: block;
  margin: $TwentyEven;
  padding: $TwentyEven;
  line-height: 30px;
  border-radius: 10px;
}

.entryForm button {
  background-color: $backgroundApp;
}
.editForm button {
  background-color: $backgroundApp;
}
.Result button {
  background-color: $backgroundApp;
}
.Result button:last-child {
  background-color: orangered;
}
.Result img {
  float: left;
  height: 180px;
}
